*{
    margin: 0;
    padding: 0;
}
.myproyekt{
    background-attachment: fixed;
    background-image: url("../images/kompfon2.jpeg");
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    padding-bottom: 90px;

}
.prayekt{
    position: relative;
    width: 90%;
    height: 380px;
    overflow: hidden;
    border: solid 1px white;
    border-radius: 10px;
    margin-left: 5%;
    animation-name: praject;
    animation-duration: 2s;
    animation-iteration-count: infinite;
}
@keyframes praject {
    0% {border: solid 2px red;}
    25% {border: solid 2px darkslategrey;}
    50% {border: solid 2px yellow;}
    75% {border: solid 2px orchid;}
    100% {border: solid 2px green;}
}
.prayekt .prayekt-img{
    width: 100%;
    height: 380px;
    transition: 1s;
    background-size: 100%;
}

@media only screen and (max-width: 576px){
.prayekt, .prayekt-img{
    height: 250px;
}

}
.yuqori-fon {
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    background-color: black;
    overflow: hidden;
    width: 100%;
    height: 0;
    transition: .5s ease;
    opacity: 0.8;
    border-bottom: 5px solid red;
}
.prayekt:hover .prayekt-img{
    transform: scale(1.3);
}
.prayekt:hover .yuqori-fon {
    top: 0;
    height: 30%;
}

.yuqori-text {
    white-space: nowrap;
    color: white;
    font-size: 35px;
    position: absolute;
    overflow: hidden;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    font-family: Noto;
}
.pastki-fon{
    position: absolute;
    bottom: -5px;
    left: 0;
    right: 0;
    background-color: black;
    overflow: hidden;
    width: 100%;
    height: 0;
    transition: .5s ease;
    border-top: 5px solid red;
    opacity: 0.8;
}
.prayekt:hover .pastki-fon {
    height: 50%;
}
.borview{
    margin: 10px 20px;
    border: dashed 1px yellow;
    width: 80px;
    height: 40px;
    border-radius: 5px;
}
 .view{
    color: darkseagreen;
    font-size: 25px;
    margin-top: -2px;
    margin-left: 5px;
     transition: 1s;
}
 .borview:hover{
     border: solid 1px red;
 }
 .view:hover{
     color: red;
 }

