*{
    margin: 0;
    padding: 0;
}
.cvfon{
    background-color: black;
    width: 60%;
    height: auto;
    margin: 0 auto;
    padding-bottom: 20px;
    padding-right: 0;
}
.savol{
    color: #CDCDCD;
    font-family: "source-code-pro";
    margin: 0;
    padding: 0;
}
.javob{
    color: white;
    position: absolute;
    left: 80px;
    font-family: "source-code-pro";
    margin: 0;
    padding: 0;
}
.chiziq{
    width: 90%;
    height: 2px;
    background-color: #CDCDCD;
    margin-top: 10px;
}
.chiziq1{
    margin-top: 35px;
}
.savol-javob{
  margin-top: 30px;
    margin-left:10%  ;
}
.savol-javob1{
    margin-top: 50px;
    margin-left:10%  ;
}
.download{
    border: dotted 2px;
    width: 20%;
    text-align: center;
    margin: 50px auto;
    height: 40px;
    border-top-color: deepskyblue;
    border-right-color: deepskyblue;
    border-bottom-color: deepskyblue;
    border-left-color: deepskyblue;
    animation-iteration-count: infinite;
    animation-name: bordercolor;
    animation-duration: 2s;
    border-radius: 5px;
}
.download .downloada{
    line-height: 30px;
    animation-name: textanim;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    color: #61dafb;
}
@keyframes bordercolor {
    0% {border: dotted 2px deepskyblue}
    15% {border-top-color: red}
    35% {border-right-color: yellow}
    55% {border-bottom-color: blueviolet}
    75% {border-left-color: darkgoldenrod}
    100% {border: dotted 2px deepskyblue}
}
@keyframes textanim {
    0%   {color: red;}
    25%  {color: yellow;}
    50%  {color: blue;}
    100% {color: white;}
}
@media only screen and (max-width: 576px){
    .cvfon{
        width: 100%;
    }
    .savol-javob, .savol-javob1{
        margin-left: 5%;
    }
    .chiziq{
        width: 95%;
    }
    .chiziq2{
        margin-top: 35px;
    }
    .chiziq1{
        margin-top: 58px;
    }
    .myfoto{
        width: 40% !important;
    }
    .dear{
        margin-left: -25px;
    }
    .download{
        width: 35%;
    }
}
