*{
    margin: 0;
    padding: 0;
}
.about{
    position: absolute;
    top: 300px;
    color: floralwhite;
    font-size: 50px;
    font-family: "Apple Color Emoji";
    left: 100px;
}
.aboutme{
    margin-top: 30px;
}
.about-text{
    font-family: Gabriola;
    font-size: 30px;
}
.about-text1{
    color: #61dafb;
    font-size: 35px;

}
.header {
    position: fixed;
    top: 0; left: 0;
    width: 100%;
    height: 60px;
    background-color: #000000;
    box-shadow: 2px 2px wheat;
    z-index: 1000;
    padding-right: 0;
    margin-right: 0;
}
.nav {
    /*display: flex;*/
    /*justify-content: space-between;*/
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    margin-left: 100px;
}
.nav .nav-link {
    text-decoration: none;
    color: white;
    padding: 0 40px;
    transition: .2s;
    font-size: 20px;
    transition: 1s ease;
}
.nav-link.active {
    color: #3AD356;
}
.nav .nav-linklogo{
    margin-right: 30px;
}
.nav-link1.active1{
    color: red;
}
.nav .nav-link2{
    font-family: Apple;
    font-size: 30px;
    margin-left: 20px;
    margin-right: 30px;
}
.nav .nav-link:hover{
    color: #3AD356;
}
.nav .laungage{
position: absolute;
    right: 150px;
}
.laungage .uzlaun{
    color: white;
    cursor: pointer;
}
.laungage .enlaun{
    color: white;
    cursor: pointer;
}
.laungage .activlan{
    color:red ;
}
.ism{
    color: deepskyblue;
    animation-name: ism;
    animation-duration: 3s;
    animation-iteration-count: infinite;
}
.developer{
    color: greenyellow;
    animation-name: developer;
    animation-duration: 3s;
    animation-iteration-count: infinite;
}
.front{
    color: sandybrown;
    animation-name: front1;
    animation-duration: 3s;
    animation-iteration-count: infinite;
}
@keyframes ism {
    0%{color: violet}
    50%{color: springgreen}
    100%{color: wheat}
}
@keyframes developer {
    0%{color: greenyellow}
    50%{color: darkmagenta}
    100%{color: skyblue}
}
@keyframes front1 {
    0%{color: olive}
    50%{color: chocolate}
    100%{color: rebeccapurple}
}
@media only screen and (max-width: 576px){
    .header{
        width: 100%;
        height: 50px;
    }
    .about{
        font-size: 25px;
        position: absolute;
        top: 100px;
        left: 50px;
    }
    .about-text{
       margin-left: 20px;
    }
    .aboutme .foto{
        margin-left: 20%;
        width: 60%;
        height: 80%;
    }
    .particles{
        height: 100%;
    }
    .foto{
        width: 30%;
        height: 80%;
    }
}
.foto{
    width: 50%;
    height: 90%;
}
.sidebarbac{
    background-color: rgba(240,240,240,0.7);
    width: 100%;
    height: 40px;
    border-bottom: 2px solid black;
}
.sidebarbac a{
    font-family: Apple;
    text-decoration: none;
    color:  #4B5CC8;
    padding: 20px 40px;
    transition: .2s;
    font-size: 18px;
    transition: 1s ease;
}
.sidebarbac:hover a{
    color:red;
}
.sidebarbac:hover{
    background-color: rgba(200,200,200,1);
}
.menu{
    margin-top: -12px;
    position: absolute;
    right: 40px;
    cursor: pointer;
}
@media only screen  and (max-width: 1200px){
    .header .cancel{
        display: none;
    }
}
@media only screen  and (min-width: 1200px){
    .header .cancel1{
        display: none;
    }
}
.mobileheader{
    display: inline-block;
}
.laungage1{
    position: absolute;
    right: 100px;
    display: inline-block;
    margin-top: -6px;
}
.activlan1{
    color: red;
}
.uzlaun1{
    cursor: pointer;
}
.enlaun1{
    cursor: pointer;
}
