*{
    margin: 0;
    padding: 0;
}
.footer{
    background-color: black;
    width: 100%;
    margin-top: 50px;
}
.foter-mobile{
    margin-right: 0;
    padding-right: 0;
}
.ruyhat{
    background-color: #B3CEF7;
    height: 400px;
    width: 90%;
    border: dashed 3px darkslategrey;
    animation-name: ruyhatborder;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    padding-bottom: 25px;
    margin: 0 auto;
}
@keyframes ruyhatborder {
    0% {border: dashed 3px deepskyblue}
    15% {border-top-color: red}
    35% {border-right-color: yellow}
    55% {border-bottom-color: blueviolet}
    75% {border-left-color: darkgoldenrod}
    100% {border: dashed 3px deepskyblue}
}
.iconbor{
    width: 40px;
    height: 40px;
    background-color: #F7DEE1;
    border-radius: 100%;
    transition: 2s;
    margin-bottom: 20px;
    cursor: pointer;
    margin-right: 0;
    padding-right: 0;
}
 .icon{
    font-size: 20px;
    margin-left: 10px;
    padding-top: 2px;

}
.insta{
     background: linear-gradient(
             210deg,#9D69C2,#D53293,#E02D6A,#F3BD2D,#F1E99D);
 }
.iconbor:hover{
    background-color: aquamarine;
}
.icon a{
    color: white;
}
@media only screen and (max-width: 576px){
 .foter-mobile{
     margin-left: 20px;
 }
