*{
    margin: 0;
    padding: 0;
}
.fonloader{
    width: 100%;
    height: 100vh;
    background-color: #030303;
}
.loader{
   width: 700px;
    height: 500px;
    margin: 100px auto;
    margin-left: 25%;
}
@media only screen and (max-width: 576px){
    .fonloader{
        width: 576px;
    }
    .loader{
        width: 200px;
        height: 150px;
        margin: 250px auto;
        margin-left: 13%;

    }
}