*{
    margin: 0;
    padding: 0;
}
.resume{
    background-attachment: fixed;
    background-image: url("../images/fonskill.jpg");
    width: 100%;
    background-repeat: no-repeat;
    background-size: 100%;
    opacity: 0.9;
    padding-bottom: 90px;
}
.resume .myskill{
    color: white;
    text-align: center;
    font-family: "Bell MT";
    padding-top: 50px;
}
.yozuv{
    margin-top: 80px;
}
.yozuv-f{
    margin-bottom: 5px;
    color: white;
    font-size: 30px;
    font-family: "Bell MT";
}
.hr{
    background-color: white;
    width: 100%;
    height: 6px;
}

.swiper-pagination .swiper-pagination-bullet {
    opacity: 0.5;
    border: greenyellow solid 1px;
    background-color: #807577;
}
.swiper-pagination .swiper-pagination-bullet-active {
    background-color:blue ;
    opacity: 1;
}
.frontlogo {
    position: relative;
    width: 100%;
}

.image {
    display: block;
    width: 100%;
   height: 200px;
}

.overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: .5s ease;
    background-color: #008CBA;
}

.frontlogo:hover .overlay {
    opacity: 0.5;
}
@media only screen and (max-width: 576px){
    .resume{
        background-size: inherit;
    }
    .image{
        height: 150px;
    }
}